import { Dropdown, DropdownProps } from "primereact/dropdown";
import React, { useEffect, useState } from "react";
import Label from "components/Label";
import { IBaseInputCommonProps } from "components/interface/Input";
import InputError from "components/InputError";
import { withAuth } from "components/hoc/withAuth";

export interface IBaseDropDownProps extends DropdownProps, IBaseInputCommonProps {}

const BaseDropDown: React.FC<IBaseDropDownProps> = (props: IBaseDropDownProps) => {
  const {
    hideLabel,
    label,
    validation,
    labelClassNames,
    hideErrorRow,
    errorClassNames,
    reportFormDirty,
    ...PrimeReactProps
  } = props;

  const {
    appendTo,
    placeholder,
    className,
    value,
    panelClassName,
    disabled,
    inputId,
    optionLabel,
    options,
    optionValue,
    isDisabledByAcl,
    isIgnoreAccessLevel,
    onChange,
    ...baseProps
  } = PrimeReactProps;

  const [stateFinalOptions, setStateFinalOptions] = useState([]);

  useEffect(() => {
    setStateFinalOptions(prepareOption(options));
  }, [value, options]);

  const prepareOption = (options: any) => {
    console.log(options, "hi this is options");

    if (options === undefined || options.length === 0) {
      return [];
    }
    if (options.length > 0 && typeof options[0] !== "object") {
      return options;
    }

    let finalOptions: any = [];
    options.forEach((option: any) => {
      if (!("isActive" in option)) {
        console.log(option);
        finalOptions.push(option);
        return finalOptions;
      }

      if (option.isActive) {
        finalOptions.push(option);
        return finalOptions;
      }

      if (optionValue && value && option[optionValue] === value) {
        finalOptions.push(option);
        return finalOptions;
      }
    });

    if (stateFinalOptions?.length > finalOptions?.length) {
      finalOptions = stateFinalOptions;
    }
    return finalOptions;
  };
  return (
    <div className="flex flex-col">
      {!hideLabel && <Label label={label} labelClassNames={labelClassNames} />}
      <Dropdown
        {...baseProps}
        appendTo={appendTo ?? "self"}
        disabled={disabled || isDisabledByAcl}
        inputId={inputId ? inputId : ""}
        optionLabel={optionLabel}
        options={stateFinalOptions}
        optionValue={optionValue}
        panelClassName={panelClassName}
        value={value}
        onChange={onChange}
        placeholder={placeholder}
        className={`${className} ${
          isDisabledByAcl
            ? "!bg-secondary-disabledBGColor !border-primary-gray-300 placeholder-secondary-gray-500 !text-primary-gray-800"
            : " "
        } `}
      />
      {!hideErrorRow && (
        <InputError
          validation={validation}
          hideErrorRow={hideErrorRow}
          errorClassNames={errorClassNames}
        />
      )}
    </div>
  );
};

export default withAuth(BaseDropDown);
